const wasm = import('wasm-aeslib');
onmessage = function (event) {
    const {type, method, msg} = event.data;
    if('aes' === type){
        wasm.then(({ Aeslib }) => {
            const { key, iv, payload } = msg;
            const aeslib = new Aeslib(key, iv);
            switch(method){
            case 'base64encode': {
                const result = aeslib.base64encode(payload);
                this.postMessage(result);
                break;
            }
            case 'fileencode': {
                const result = aeslib.fileencode(payload);
                this.postMessage(result);
                break;
            }
            case 'base64decode': {
                try{
                    const result = aeslib.base64decode(payload);
                    this.postMessage(result);
                }catch(e){
                    this.postMessage(null);
                }
                break;
            }
            case 'filedecode': {
                const result = aeslib.filedecode(payload);
                this.postMessage(result);
                break;
            }
            }
        });
    }
};